
@media screen and (max-width: 600px) {
    /* Navbar section................ */
    .nav-item{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -1;
        transition: .3s;
        background-color: rgb(0,0,0,.9);
    }
    .nav-item li{
        padding: 1rem 0;
    }
    .nav-item li a{
        font-size: 2rem;
    }
    .nav-item.active{
        left: 0;
    }
    
.sidebar{
    display: initial;
}
/* About section............... */
.text-about h1{
        display: none;
}

.tex{
    display: initial;
}

